* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --full-width: 100%;
  --page-height: 100vh;
  --container-width: 80%;
  --bgColor: #639;
  --white: #FFFFFF;
  --font-family: 'Inter', sans-serif;
}

body {
  font-family: var(--font-family);
}

.--container {
  width: var(--container-width);
  margin-inline: auto;
}

.--dFlex {
  display: flex;
}

@media(max-width:580px) {
  .--dFlex {
    flex-direction: column;
  }

  .--form .select--input {
    width: var(--full-width);
  }

}

.section--container {
  width: var(--full-width);
  min-height: var(--page-height);
  background-color: var(--bgColor);
}

nav {
  padding: 20px 0;
  color: var(--white);
  box-shadow: 5px 5px 15px -1px rgba(0, 0, 0, 0.75);
}

nav .--container>h1 {
  font-size: 2em;
  font-weight: 300;
}

.form--container {
  min-height: var(--page-height);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.--form {
  background-color: var(--white);
  padding: 50px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 5px 5px 15px -1px rgba(0, 0, 0, 0.75);
}

fieldset {
  width: 100%;
  padding: 1rem;
}

legend {
  opacity: .5;
}

.--form .form--input {
  width: 100%;
  margin-bottom: 20px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid gray;
  padding: 5px;
  outline: 0;
}

textarea {
  width: 100%;
  border-radius: 5px;
  border: 1px solid gray;
  padding: 5px;
  outline: 0;
}

.--form .small--input {
  margin-bottom: 20px;
  width: 20px;
  border: 1px solid gray;
  padding: 5px;
  font-family: 'Inter', sans-serif;
}

.--form .select--input {
  height: 40px;
  border: 1px solid gray;
  outline: 0;
}

input::placeholder,
textarea::placeholder {
  font-style: italic;
  text-align: center;
}

#error-message {
  color: red;
}

.--enqItem {
  margin-block-end: 10px;
}

.--form .--dBlock {
  display: block;
}

.submit--btn {
  padding: 15px 30px;
  color: var(--white);
  outline: 0;
  border: none;
  border-radius: 5px;
  background-color: var(--bgColor);

}